import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import MaterialTable from 'material-table'
import tableIcons from './tableIcons'
import Paper from '@material-ui/core/Paper';


const useStyles = makeStyles({
    root: {
        padding: 10,
        width: '99%',
        margin: 10
    },
});

function MatTable(props) {
    const classes = useStyles();

    return (
        <Paper className={classes.root} >
            <MaterialTable
                style={{ width: '100%', margin: 5, overflowX: "scroll" }}
                icons={tableIcons}
                //title={"Quantity Move List :" + (props.qtyMoveList)? 0:props.qtyMoveList.length}
                title={"Quantity Move List :" + props.qtyMoveList.length + "รายการ"}
                columns={[
                    { title: 'id', field: 'id' },
                    { title: 'lot', field: 'lot' },
                    { title: 'From location', field: 'loc' },
                    { title: 'item', field: 'item',width:400 },
                    { title: 'เส้น/มัด', field: 'qty1',type:'numeric' },
                    { title: 'unit', field: 'u_m' },
                ]}
                data={props.qtyMoveList}
                options={{
                    search: false,
                    paging: false,
                    maxBodyHeight: '45vh',
                    minBodyHeight: '45vh',
                }}
            />
        </Paper>

    )
}

export default MatTable;
